
import { Link } from 'react-router-dom';
import "./style.css";

const Home = () => {
    return (
        <div className="home">
          <div className="title">
              <h1>
                  <p>Hi,</p>
                  <p>I am Shubham Kamble</p>
                  <p>IT Engineer</p>
              </h1>
              <Link to="/about">
                  <button>More Info</button>
              </Link>
          </div>
          <div className="person">
              <img src={`${process.env.PUBLIC_URL}/myAvatar1.png`} alt="myAvatar"/>
          </div>
        </div>
    );
};

export default Home;
